.wrapper{display:flex;flex-direction:column;justify-content:space-between;height:100%}.header{border-bottom:1px solid #5181b8}.back{text-align:left;display:flex;transition:all .3s ease;padding:10px}.back p{color:#5181b8}.back svg{width:10px;margin-right:8px}.back svg path{fill:#5181b8}.back:active p{color:#1a66bb;font-size:15px}.footer{display:flex;justify-content:space-between}.footer input{padding:10px 20px;border-radius:5px;border:1px solid #1a66bb;flex:0 1 88%}.footer button{padding:5px 10px;background-color:#0d61bc;color:#fff;border-radius:5px;flex:0 1 10%}.messageField{position:relative;border-bottom:1px solid #0d61bc;padding:40px 0 20px 0}.mapText{color:#767474;padding:5px;font-size:18px;color:#000}.mapTime{position:relative;color:#aaa8a8;padding:5px;font-size:14px;position:absolute;top:58px;right:36px}.mapTime::after{content:"✔";position:absolute;top:2px;right:-12px}.text{padding:20px}